    <h1>France</h1> 
La France est un État souverain transcontinental dont le territoire s'étend 
en Europe de l'Ouest et outremer (Indien, Atlantique, Pacifique et Amérique 
du Sud). Le pays a des frontières terrestres avec la Belgique, le 
Luxembourg, l'Allemagne, la Suisse, l'Italie, l'Espagne, Monaco et 
l'Andorre en Europe, auxquelles s'ajoutent les frontières terrestres avec 
