Pacifique et l'océan Indien, lui permettant de bénéficier de la deuxième 
plus vaste zone économique exclusive du monde. 
La France est une république constitutionnelle unitaire ayant un régime 
semi-présidentiel. Elle a pour capitale Paris et pour langue officielle le 
français, langue de l'administration depuis 1539. Au 1er janvier 2022, la 
population de la France est d'environ 67,8 millions d'habitants. 
